/* Style the body */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
}

/* Container for the counter app */
.counter-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  max-width: 350px;
  width: 100%;
  text-align: center;
}

/* Style for buttons */
.counter-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 20px;
}

.button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #0056b3;
}

/* Style for the counter display */
.counter-display {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Mobile view adjustments */
@media (max-width: 480px) {
  .counter-container {
    padding: 15px;
  }

  .counter-display {
    font-size: 36px;
  }

  .button {
    padding: 8px 16px;
    font-size: 14px;
  }
}

/* Tablet view adjustments */
@media (max-width: 768px) {
  .counter-container {
    padding: 20px;
  }

  .counter-display {
    font-size: 40px;
  }

  .button {
    padding: 10px 18px;
    font-size: 15px;
  }
}
